const actions = {
  getItemCategories: () => ({
    type: "GET_ITEM_CATEGORIES_REQUEST",
  }),
  storeItemCategory: body => ({
    type: "STORE_ITEM_CATEGORY_REQUEST",
    payload: { body: body }
  }),
  showItemCategory: id_item_category => ({
    type: "SHOW_ITEM_CATEGORY_REQUEST",
    payload: { id_item_category: id_item_category }
  }),
  updateItemCategory: (id_item_category, body) => ({
    type: "UPDATE_ITEM_CATEGORY_REQUEST",
    payload: { id_item_category: id_item_category, body: body }
  }),
  activateItemCategory: id_item_category => ({
    type: "ACTIVATE_ITEM_CATEGORY_REQUEST",
    payload: { id_item_category: id_item_category }
  }),
  deactivateItemCategory: id_item_category => ({
    type: "DEACTIVATE_ITEM_CATEGORY_REQUEST",
    payload: { id_item_category: id_item_category }
  }),
  clearItemCategory: () => ({
    type: "CLEAR_ITEM_CATEGORY"
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
