import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  Select,
  Col,
  Row,
  Divider,
  TreeSelect
} from "antd";

// const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

class ItemCategoryForm extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { user } = this.props.auth;
    const itemCategory = this.props.itemCategory ? this.props.itemCategory : [];
    const { loadingSubmit } = itemCategory;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const loop = array =>
      array.map(data => {
        let perData;
        if (data.is_active === 0) {
          perData = {
            title: data.item_category_code + " (" + data.item_category_name + ")",
            value: data.id_item_category,
            key: data.id_item_category,
            disabled: true,
            //children : data.children ? loop(data.children) : null
          }
        }
        else {
          perData = {
            title: data.item_category_code + " (" + data.item_category_name + ")",
            value: data.id_item_category,
            key: data.id_item_category,
            children: data.children ? loop(data.children) : null
          }
        }
        return perData
      })


    // const used = data.is_used;
    const error = itemCategory.errorField ? itemCategory.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="itemCategories.add.subtitle.data" />
        </Divider>
        {/* {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="itemCategories.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : null} */}

        <Row>
          <Col span={24}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="itemCategories.parent" />}
            >
              {getFieldDecorator("id_item_category_parent", {
                // initialValue: data.id_item_category_parent,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <TreeSelect
                  style={{ width: "100%" }}
                  allowClear={true}
                  showSearch={true}
                  treeNodeFilterProp="title"
                  dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
                  treeData={loop(this.props.itemCategory.item_categories)}
                  treeDefaultExpandAll
                // onChange={this.onChange}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="itemCategories.code" />}
              validateStatus={
                error.item_category_code || getFieldError("item_category_code")
                  ? "error"
                  : null
              }
              help={
                error.item_category_code
                  ? error.item_category_code
                  : getFieldError("item_category_code")
              }
            >
              {getFieldDecorator("item_category_code", {
                //initialValue: newItemCategory ? null : data.item_category_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                // addonBefore={prefix}
                // disabled={used ? true : false}
                />
              )}
            </Form.Item>


            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="itemCategories.name" />}
            >
              {getFieldDecorator("item_category_name", {
                //  initialValue: data.item_category_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input
              // disabled={used ? true : false} 
              />)}
            </Form.Item>

          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <InputGroup compact>
              <Col>
                <Form.Item {...tailFormItemLayout}>
                  <Button
                    type="primary"
                    htmlType="submit"
                    style={{ marginRight: "15px" }}
                    loading={loadingSubmit ? true : false}
                  >
                    <IntlMessages id="button.submit" />
                  </Button>
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
        </Row>
      </Form>
    );
  }
}

const WrappedItemCategoryForm = Form.create({ name: "create_item_category" })(ItemCategoryForm);

export default WrappedItemCategoryForm;
