import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import notification from "../../../../components/notification";
import { Input, Select, Tree, Row, Col, Divider, Empty, Spin } from "antd";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/itemCategory/actions";
import CardWrapper, { Box } from "../../../App/app.style";
import CreateForm from "./createForm";
import UpdateForm from "./updateForm";
import getLanguage from "../../../App/MyLanguange"

// const Option = Select.Option;
const Search = Input.Search;
const { TreeNode } = Tree;
const redirectTo = "/dashboard/setup/menus";

// Searching Item
const getParentKey = (key, tree) => {
  let parentKey;
  for (let i = 0; i < tree.length; i++) {
    const node = tree[i];
    if (node.children) {
      if (
        node.children.some(
          item_category => item_category.id_item_category === key
        )
      ) {
        parentKey = node.id_item_category;
      } else if (getParentKey(key, node.children)) {
        parentKey = getParentKey(key, node.children);
      }
    }
  }
  return parentKey;
};

class ItemCategory extends Component {
  state = {
    expandedKeys: [],
    searchValue: "",
    autoExpandParent: true,
    id_item_category: null,
    status: 0
    // 0 empty , 1 create , 2 update
  };

  // Get All data diawal
  componentDidMount() {
    const { getItemCategories } = this.props;
    getItemCategories();
  }

  // Display notif
  componentDidUpdate() {
    // Display error notif
    if (
      !this.props.itemCategory.loadingSubmit &&
      !this.props.itemCategory.loadingItemCategory &&
      !this.props.itemCategory.loadingItemCategories &&
      this.props.itemCategory.error
    ) {
      notification("error", this.props.itemCategory.error);
      this.props.clearError();
    }

    // Display success notif
    if (
      !this.props.itemCategory.loadingSubmit &&
      !this.props.itemCategory.loadingItemCategory &&
      !this.props.itemCategory.loadingItemCategories &&
      this.props.itemCategory.success
    ) {
      notification("success", this.props.itemCategory.success);
      this.props.clearSuccess();

      // Clear state new ItemCategory
      this.setState({
        ...this.state,
        id_item_category: this.props.itemCategory.item_category
          ? this.props.itemCategory.item_category.id_item_category
          : null
      });
    }
  }

  // Handle change search
  handleSearchChange = e => {
    const { item_categories_list, item_categories } = this.props.itemCategory;
    const value = e.target.value;
    const expandedKeys = item_categories_list.map(item_category => {
      const name =
        item_category.item_category_code +
        " " +
        item_category.item_category_name;
      if (name.toLowerCase().indexOf(value.toLowerCase()) > -1) {
        return getParentKey(item_category.id_item_category, item_categories);
      }
      return null;
    });

    this.setState({
      expandedKeys,
      searchValue: value,
      autoExpandParent: true
    });
  };

  // Handle Add Button
  handleAddButton = () => {
    const { clearItemCategory } = this.props;

    this.setState({
      id_item_category: null,
      status: 1
    });
    clearItemCategory();
  };

  // Handle select tree
  handleSelectTree = e => {
    const { showItemCategory } = this.props;
    this.props.clearErrorField();

    const value = e[0] ? e[0] : "";

    if (value) {
      showItemCategory(value);
    }

    this.setState({
      ...this.state,
      id_item_category: value ? value : null,
      status: value ? 2 : 0
    });
  };

  // Handle expand tree
  onExpand = expandedKeys => {
    this.setState({
      expandedKeys,
      autoExpandParent: false
    });
  };

  // Handle submit Create
  handleSubmitCreate = value => {
    const { storeItemCategory } = this.props;
    storeItemCategory(value);
  };

  handleSubmitUpdate = value => {
    const { updateItemCategory } = this.props;
    const { id_item_category } = this.state;
    updateItemCategory(id_item_category, value);
  };

  // Handle Activate item_category
  handleActivate = id_item_category => {
    const { activateItemCategory } = this.props;
    // console.log(id_item_category);
    activateItemCategory(id_item_category);
  };

  // Handle dectivate item_category
  handleDeactivate = id_item_category => {
    const { deactivateItemCategory } = this.props;
    // console.log(id_item_category);
    deactivateItemCategory(id_item_category);
  };

  render() {
    // console.log("/\\/");
    // console.log(this.state);
    const { item_category, item_categories } = this.props.itemCategory;
    const {
      loadingSubmit,
      loadingItemCategory,
      loadingItemCategories
    } = this.props.itemCategory;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const checkLoading = () => {
      if (loadingItemCategories) {
        return true;
      }
      return false;
    };

    const {
      searchValue,
      expandedKeys,
      autoExpandParent
      // newItemCategory
    } = this.state;

    const loop = data =>
      data.map(item_category => {
        const name =
          item_category.item_category_code +
          " (" +
          item_category.item_category_name +
          ")";
        const key = name;
        const index = key.toLowerCase().indexOf(searchValue.toLowerCase());
        const beforeStr = name.substr(0, index);
        const str = name.substr(index, searchValue.length);
        const afterStr = name.substr(index + searchValue.length);
        const title =
          index > -1 ? (
            <span>
              {beforeStr}
              <span style={{ color: "#000000", fontWeight: "bold" }}>
                {str}
              </span>
              {afterStr}
            </span>
          ) : (
              <span>{name}</span>
            );
        if (item_category.children) {
          return (
            <TreeNode
              key={item_category.id_item_category}
              title={
                item_category.is_active === 0 ? (
                  <span style={{ color: "#f5222d" }}>{title}</span>
                ) : (
                    title
                  )
              }
            >
              {loop(item_category.children)}
            </TreeNode>
          );
        }
        return <TreeNode key={item_category.id_item_category} title={title} />;
      });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="itemCategories.pageHeader" />}
            style={{ margin: 0 }}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Item Category") ? (
                <Col span={12}>
                  <Row type="flex" justify="end">
                    <Col>
                      <Button type="primary" onClick={this.handleAddButton}>
                        <IntlMessages id="itemCategories.add" />
                      </Button>
                    </Col>
                  </Row>
                </Col>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            {/* <InputGroup compact> */}
            <Col span={12}>
              <Col>
                <Search
                  placeholder={getLanguage({ id: "sidebar.itemCategory" })}
                  disabled={checkLoading()}
                  allowClear
                  onChange={this.handleSearchChange}
                />
              </Col>
            </Col>

            {/* </InputGroup> */}
          </div>

          <CardWrapper>
            <Col span={12}>
              <Col span={23}>
                {checkLoading() ? (
                  <div className="spinStyle">
                    <Spin />
                  </div>
                ) : item_categories.length === 0 ? (
                  <Empty description={<IntlMessages id="noData" />} />
                ) : (
                      <Tree
                        showIcon
                        showLine
                        onExpand={this.onExpand}
                        expandedKeys={expandedKeys}
                        autoExpandParent={autoExpandParent}
                        onSelect={this.handleSelectTree}
                      >
                        {loop(item_categories)}
                      </Tree>
                    )}
              </Col>
              <Col span={1}>
                <Divider type="vertical" style={{ height: "100vh" }} />
              </Col>
            </Col>
            <Col span={12}>
              {loadingItemCategory && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin />
                </div>
              ) : this.state.status === 2 ? (
                <UpdateForm
                  submit={this.handleSubmitUpdate}
                  itemCategory={this.props.itemCategory}
                  auth={this.props.auth}
                  activate={this.handleActivate}
                  deactivate={this.handleDeactivate}
                />
              ) : this.state.status === 1 ? (
                <CreateForm
                  submit={this.handleSubmitCreate}
                  itemCategory={this.props.itemCategory}
                  auth={this.props.auth}
                />
              ) : (
                      <Empty
                        description={<IntlMessages id="itemCategories.noSelection" />}
                      />
                    )}
            </Col>
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { itemCategory: state.itemCategory, auth: state.auth };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(ItemCategory);
